import React, { useMemo } from "react";
import { Col, Row } from "react-bootstrap";

// needed asssets

// svgs
import { ReactComponent as Search } from "assets/svgs/search.svg";

// images

// needed styles
import styles from "./index.module.scss";
import { FormattedMessage, useIntl } from "react-intl";
import { useSelector } from "react-redux";

const SearchEmployees = () => {
  const intl = useIntl();
  const {
    meta: { locale },
    empolyee: { specialties, nationalties },
  } = useSelector((state) => state);
  const specialtiesOptions = useMemo(
    () => [
      ...specialties.map((specialty) => ({
        label: locale === "ar" ? specialty.name_ar : specialty.name_en,
        value: specialty._id,
      })),
    ],
    [specialties, locale]
  );
  return (
    <form className={styles["wrapper"]}>
      <Row>
        <h3 className={styles["header"]}>
          <FormattedMessage id="lookForEmployee" />
        </h3>
      </Row>
      <Row>
        <Col lg={6}>
          <div className={`${styles["search"]}`}>
            <select
              type="search"
              name="search"
              // {...register("search", { required: true })}
            >
              <option value={null} selected={true}>
                {/* ابحث عن المهنه */}
                {intl.formatMessage({ id: "lookForEmployee" })}
                {/* <FormattedMessage id="lookForEmployee" /> */}
              </option>
              {specialtiesOptions.map((specialty) => (
                <option value={specialty.value}>{specialty.label}</option>
              ))}
            </select>
            {/* <Search /> */}
          </div>
        </Col>
        <Col lg={6}>
          <div className={`${styles["select"]}`}>
            <select type="search">
              <option value="" disabled hidden>
                اختر الدوله
              </option>
              {nationalties.map((nationalty) => (
                <option key={nationalty.id}>{nationalty?.[locale]}</option>
              ))}
            </select>
          </div>
        </Col>
      </Row>
      <Row className="justify-content-center">
        <button className={styles["button"]}>
          <FormattedMessage id="search" />
        </button>
      </Row>
    </form>
  );
};

export default SearchEmployees;
