import React from "react";
// import HeroSection from "./HeroSection/HeroSection";
import SearchEmployees from "./SearchEmployees/SearchEmployees";

import OurServices from "./OurServices/OurServices";
import { Col, Container, Row } from "react-bootstrap";
import HotelsSlider from "./HotelsSlider/HotelsSlider";
import ShowCaseItem from "./ShowCaseItem/ShowCaseItem";

//asests
import Map from "assets/images/map.png";

import Service1 from "assets/images/services-1.jpeg";
import Service2 from "assets/images/services-2.jpg";
import Service3 from "assets/images/services-3.jpeg";
import Service4 from "assets/images/services-4.jpeg";

import { ReactComponent as Showcase1 } from "assets/svgs/showcase1.svg";
import { ReactComponent as Showcase2 } from "assets/svgs/showcase2.svg";
import { ReactComponent as Showcase3 } from "assets/svgs/showcase3.svg";

// styles
import styles from "./index.module.scss";
import BlueButton from "components/shared/BlueButton/BlueButton";
import { FormattedMessage, useIntl } from "react-intl";
import HeroSlider from "./HeroSlider/HeroSlider";

export const HomePage = () => {
  const intl = useIntl();

  return (
    <>
      <div className={styles["hero-section"]}>
        <HeroSlider />
        <div className={styles["search-overlay"]}>
          <SearchEmployees />
        </div>
      </div>

      <Container>
        <Row className={styles["row-gap"]}>
          <h1>{intl.formatMessage({ id: "our-services" })}</h1>
        </Row>
        <div className={styles["services-container"]}>
          <OurServices image={Service1} text={"tourist-visas"} />

          <OurServices image={Service2} text={"trips"} />

          <OurServices image={Service3} text={"inbound-tourism"} />

          <OurServices image={Service4} text={"overseas-employment"} />
        </div>
        <Row>
          <h3 className={styles["most-search"]}>
            {intl.formatMessage({ id: "our-search" })}
          </h3>
        </Row>
        <Row>
          <HotelsSlider />
        </Row>

        <Row>
          <BlueButton />
        </Row>
      </Container>

      <div className={styles["show-case"]}>
        <Container fluid>
          <Row>
            <Col xs={12} lg={4}>
              <ShowCaseItem
                number={1}
                text={<FormattedMessage id="showCaseStep1" />}
                svg={<Showcase1 />}
              />
            </Col>
            <Col xs={12} lg={4}>
              <ShowCaseItem
                number={2}
                text={<FormattedMessage id="showCaseStep2" />}
                svg={<Showcase2 />}
              />
            </Col>
            <Col xs={12} lg={4}>
              <ShowCaseItem
                number={3}
                text={<FormattedMessage id="showCaseStep3" />}
                svg={<Showcase3 />}
              />
            </Col>
          </Row>

          <Row>
            <div className={styles["map-text"]}>
              <p>
                <FormattedMessage id="showCaseHeader1" />
              </p>
              <span>
                <FormattedMessage id="showCaseHeader2" />
              </span>
            </div>
          </Row>
          <Container>
            <Row>
              <div className={styles["map-image"]}>
                <img src={Map} alt="map" />
              </div>
            </Row>
          </Container>
        </Container>
      </div>
    </>
  );
};
